import React, { useState } from "react";
import "../styles/App.css";
import { GiHamburgerMenu } from "react-icons/gi";
import { useTranslation } from "react-i18next";

import { Link } from "react-scroll";

const Navbar = () => {
  const [t, i18n] = useTranslation("global");
  const [navbarClicked, setNavbarClicked] = useState(false);
  let [lang_en, setLangEn] = useState(1);
  const setEs = () => {
    setLangEn(0);
    i18n.changeLanguage("es");
  };
  const setEn = () => {
    setLangEn(1);
    i18n.changeLanguage("en");
  };

  return (
    <div className="navbar responsive-navbar">
      <h1 className="logo">&lt;Jonathan/&gt;</h1>

      <div
        className="nav-links"
        style={{
          transform: navbarClicked ? "translateX(0px)" : "",
        }}
      >
        <Link
          onClick={lang_en ? setEs : setEn}
          className="lang-xs-btn lang-btn "
          smooth={true}
          duration={1000}
        >
          {lang_en ? "Es" : "En"}
        </Link>
        <Link
          to="header"
          onClick={() => setNavbarClicked(!navbarClicked)}
          className="nav-link"
          smooth={true}
          duration={1000}
        >
          {t("navbar.menu.home")}
        </Link>

        <Link
          to="skills"
          onClick={() => setNavbarClicked(!navbarClicked)}
          className="nav-link"
          smooth={true}
          duration={1000}
        >
          {t("navbar.menu.skills")}
        </Link>

        <Link
          to="projects"
          onClick={() => setNavbarClicked(!navbarClicked)}
          className="nav-link"
          smooth={true}
          duration={1000}
        >
          {t("navbar.menu.projects")}
        </Link>

        <Link
          to="aboutMe"
          onClick={() => setNavbarClicked(!navbarClicked)}
          className="nav-link"
          smooth={true}
          duration={1000}
        >
          {t("navbar.menu.aboutMe")}
        </Link>
      </div>
      <div
        className={navbarClicked ? "responsive-black" : ""}
        onClick={() => setNavbarClicked(!navbarClicked)}
      ></div>
      <a
        className="contact-btn"
        href={t("navbar.resume.link")}
        target="_blank"
        rel="noopener noreferrer"
      >
        {t("navbar.resume.text")}
      </a>
      <span
        onClick={lang_en ? setEs : setEn}
        className="lang-btn"
        data-aos="fade-out"
      >
        {lang_en ? "Es" : "En"}
      </span>
      <GiHamburgerMenu
        className="nav-burger"
        onClick={() => setNavbarClicked(!navbarClicked)}
      />
    </div>
  );
};

export default Navbar;
